import React from 'react';
import PropTypes from 'prop-types';
import {
  Card,
  CardContent,
  CardHeader,
  FormControlLabel,
  Grid,
  Stack,
  Switch,
  TextField,
  Typography,
} from '@mui/material';

const WatchAdForm = ({ formik }) => {
  const { values, errors, touched, getFieldProps } = formik;
  return (
    <Grid container spacing={3}>
      <Grid item md={12}>
        <Stack spacing={3}>
          <Card>
            <CardHeader title="Task Detail" />
            <CardContent>
              <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
                <TextField
                  // fullWidth
                  size="small"
                  label="Task Name"
                  {...getFieldProps('name')}
                  error={Boolean(touched.name && errors.name)}
                  helperText={touched.name && errors.name}
                />
                {/* <TextField
                  select
                  fullWidth
                  size="small"
                  label="Task Type"
                  {...getFieldProps(`taskType`)}
                  SelectProps={{ native: true }}
                  error={Boolean(touched.taskType && errors.taskType)}
                  helperText={touched.taskType && errors.taskType}
                >
                  {['Video', 'Video With Sound'].map((option) => (
                    <option key={option} value={option}>
                      {option}
                    </option>
                  ))}
                </TextField> */}
              </Stack>
            </CardContent>
          </Card>

          <Card>
            <CardHeader title="Rewards" />
            <CardContent>
              <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
                <TextField
                  size="small"
                  label="Total Budget"
                  type="number"
                  {...getFieldProps('totalBudget')}
                  error={Boolean(touched.totalBudget && errors.totalBudget)}
                  helperText={(touched.totalBudget && errors.totalBudget) || '50000 tokens available - View Detail'}
                />
                <TextField
                  size="small"
                  label="Per User"
                  {...getFieldProps('perUser')}
                  error={Boolean(touched.perUser && errors.perUser)}
                  helperText={
                    (touched.perUser && errors.perUser) ||
                    `Estimated user number: budget/tokenPerUser = ${
                      Number.isNaN(values.totalBudget / values.perUser) ? 0 : values.totalBudget / values.perUser
                    } users`
                  }
                />
                <TextField
                  size="small"
                  label="Star Per User"
                  type="number"
                  {...getFieldProps('starPerUser')}
                  error={Boolean(touched.starPerUser && errors.starPerUser)}
                  helperText={touched.starPerUser && errors.starPerUser}
                />
              </Stack>
            </CardContent>
          </Card>

          <Card>
            <CardHeader title="Audience" />
            <CardContent>
              <Stack alignItems="center" direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
                <TextField
                  select
                  fullWidth
                  size="small"
                  label="Select Tier"
                  {...getFieldProps(`tier`)}
                  SelectProps={{ native: true }}
                  error={Boolean(touched.tier && errors.tier)}
                  helperText={touched.tier && errors.tier}
                >
                  {['Public', 'Specific Group', 'Level2', 'Level3', 'Bonus Winner Task'].map((option) => (
                    <option key={option} value={option}>
                      {option}
                    </option>
                  ))}
                </TextField>
                {values.tier === 'Bonus Winner Task' && (
                  <TextField
                    select
                    fullWidth
                    size="small"
                    label="Associated Bonus"
                    {...getFieldProps(`associatedBonus`)}
                    SelectProps={{ native: true }}
                    error={Boolean(touched.associatedBonus && errors.associatedBonus)}
                    helperText={touched.associatedBonus && errors.associatedBonus}
                  >
                    {['Bonus 1', 'Bonus 2', 'Bonus 3'].map((option) => (
                      <option key={option} value={option}>
                        {option}
                      </option>
                    ))}
                  </TextField>
                )}
                <Stack direction="row" justifyContent="space-around">
                  <Typography variant="caption">
                    In case of public, the task goes to the activity feed and all users can see
                  </Typography>
                  <FormControlLabel
                    labelPlacement="start"
                    control={<Switch {...getFieldProps('isPublic')} checked={values.isPublic} />}
                    label={<Typography variant="subtitle2">Public</Typography>}
                    sx={{ mx: 0, width: 1, justifyContent: 'center' }}
                  />
                </Stack>
              </Stack>
            </CardContent>
          </Card>
        </Stack>
      </Grid>
    </Grid>
  );
};

WatchAdForm.propTypes = {
  formik: PropTypes.object,
};

export default WatchAdForm;
