import PropTypes from 'prop-types';
import { FieldArray, getIn } from 'formik';
import { isValid as isValidDate } from 'date-fns';

import { Icon } from '@iconify/react';
import PlusFill from '@iconify/icons-eva/plus-fill';
import { Add as AddIcon, Delete as DeleteIcon } from '@mui/icons-material';
import { DateTimePicker } from '@mui/lab';
import { Button, Card, CardContent, CardHeader, Grid, IconButton, Stack, TextField } from '@mui/material';

// utils
import colors from '../../../utils/constants/colors';
// ----------------------------------------------------------------------

const initialQuestion = {
  title: '',
  options: [''],
};

const SurveyAndRewardsForm = ({ formik }) => {
  const { errors, values, touched, setFieldValue, getFieldProps } = formik;

  return (
    <Grid container spacing={3}>
      <Grid item md={12}>
        <Stack spacing={3}>
          <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
            <TextField
              fullWidth
              size="small"
              label="Name"
              {...getFieldProps('name')}
              error={Boolean(touched.name && errors.name)}
              helperText={touched.name && errors.name}
            />
          </Stack>

          <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
            <TextField
              fullWidth
              multiline
              rows={3}
              size="small"
              label="Description"
              {...getFieldProps('description')}
              error={Boolean(touched.description && errors.description)}
              helperText={touched.description && errors.description}
            />
          </Stack>

          <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
            <TextField
              fullWidth
              select
              size="small"
              label="Percentage Color"
              {...getFieldProps('percentageColor')}
              SelectProps={{ native: true }}
              error={Boolean(touched.percentageColor && errors.percentageColor)}
              helperText={touched.percentageColor && errors.percentageColor}
            >
              <option value="" />
              {colors.map((color) => (
                <option value={color.value} key={color.value}>
                  {color.label}
                </option>
              ))}
            </TextField>
            <TextField
              fullWidth
              size="small"
              label="Star Per User"
              type="number"
              {...getFieldProps('starPerUser')}
              error={Boolean(touched.starPerUser && errors.starPerUser)}
              helperText={touched.starPerUser && errors.starPerUser}
            />
          </Stack>

          <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
            <DateTimePicker
              label="Start date"
              value={values.startDate}
              inputFormat="dd/MM/yyyy hh:mm a"
              onChange={(date) => (isValidDate(date) ? setFieldValue('startDate', date) : null)}
              renderInput={(params) => (
                <TextField
                  {...params}
                  size="small"
                  fullWidth
                  error={Boolean(touched.startDate && errors.startDate)}
                  helperText={touched.startDate && errors.startDate}
                  sx={{ mb: 3 }}
                />
              )}
            />
            <DateTimePicker
              label="End date"
              value={values.endDate}
              inputFormat="dd/MM/yyyy hh:mm a"
              onChange={(date) => (isValidDate(date) ? setFieldValue('endDate', date) : null)}
              renderInput={(params) => (
                <TextField
                  {...params}
                  size="small"
                  fullWidth
                  error={Boolean(touched.endDate && errors.endDate)}
                  helperText={touched.endDate && errors.endDate}
                  sx={{ mb: 3 }}
                />
              )}
            />
          </Stack>

          <FieldArray
            name="surveyQuestions"
            render={(questionArrayHelpers) => (
              <Card>
                <CardHeader
                  title="Questions"
                  action={
                    <Button
                      variant="contained"
                      startIcon={<Icon icon={PlusFill} />}
                      onClick={() => questionArrayHelpers.push(initialQuestion)}
                    >
                      Add New
                    </Button>
                  }
                />
                <CardContent>
                  <Stack spacing={2}>
                    {values.surveyQuestions.map((question, questionIndex) => {
                      console.log('question index: ', questionIndex);
                      return (
                        <Card key={questionIndex}>
                          <CardHeader
                            title={`Question ${questionIndex + 1}`}
                            titleTypographyProps={{ variant: 'h8' }}
                          />
                          <CardContent>
                            <Stack spacing={1}>
                              <TextField
                                fullWidth
                                size="small"
                                label="Title"
                                {...getFieldProps(`surveyQuestions.${questionIndex}.title`)}
                                error={Boolean(
                                  getIn(touched, `surveyQuestions.${questionIndex}.title`) &&
                                    getIn(errors, `surveyQuestions.${questionIndex}.title`)
                                )}
                                helperText={
                                  getIn(touched, `surveyQuestions.${questionIndex}.title`) &&
                                  getIn(errors, `surveyQuestions.${questionIndex}.title`)
                                }
                              />
                              <FieldArray
                                name={`surveyQuestions.${questionIndex}.options`}
                                render={(optArrayHelpers) => (
                                  <>
                                    {values.surveyQuestions[questionIndex].options.map((option, optionIndex) => {
                                      const optionFieldName = `surveyQuestions.${questionIndex}.options.${optionIndex}`;
                                      return (
                                        <Stack key={optionIndex} direction="row" spacing={1}>
                                          <TextField
                                            fullWidth
                                            size="small"
                                            label={`Option ${optionIndex + 1}`}
                                            {...getFieldProps(optionFieldName)}
                                            error={Boolean(
                                              getIn(touched, optionFieldName) && getIn(errors, optionFieldName)
                                            )}
                                            helperText={
                                              getIn(touched, optionFieldName) && getIn(errors, optionFieldName)
                                            }
                                          />
                                          <IconButton
                                            color="error"
                                            aria-label="delete"
                                            onClick={() => optArrayHelpers.remove(optionIndex)}
                                          >
                                            <DeleteIcon />
                                          </IconButton>
                                        </Stack>
                                      );
                                    })}
                                    <IconButton
                                      color="primary"
                                      aria-label="plus"
                                      onClick={() => optArrayHelpers.push('')}
                                    >
                                      <AddIcon />
                                    </IconButton>
                                  </>
                                )}
                              />
                            </Stack>
                          </CardContent>
                        </Card>
                      );
                    })}
                  </Stack>
                </CardContent>
              </Card>
            )}
          />
        </Stack>
      </Grid>
    </Grid>
  );
};

SurveyAndRewardsForm.propTypes = {
  formik: PropTypes.object,
};

export default SurveyAndRewardsForm;
