import * as Yup from 'yup';
import PropTypes from 'prop-types';
import React, { useState } from 'react';
import { useSnackbar } from 'notistack';
import { useNavigate } from 'react-router-dom';
import { Form, FormikProvider, useFormik } from 'formik';

// material
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Stack, Button } from '@mui/material';
// paths
import { PATH_DASHBOARD } from '../../routes/paths';
// validations
import {
  CampaignTypeSchema,
  TaskAndRewardsSchema,
  TaskDetailSchema,
  ContestAndRewardsSchema,
  ContestDetailSchema,
  SurveyAndRewardsSchema,
  WatchAdSchema,
  WatchAdDetailSchema,
} from './validations';
// components
import Steps from '../Steps';
import CampaignTypeForm from './CampaignTypeForm';
import TaskAndRewardsForm from './task/TaskAndRewardsForm';
import TaskDetailForm from './task/TaskDetailForm';
import ContestAndRewardsForm from './contest/ContestAndRewardsForm';
import ContestDetailForm from './contest/ContestDetailForm';
import SurveyAndRewardsForm from './survey/SurveyAndRewardsForm';
import WatchAdForm from './watch-ad/WatchAdForm';
import WatchAdDetailForm from './watch-ad/WatchAdDetailForm';

// ----------------------------------------------------------------------

const STEPS = ['Campaign Type', 'Task & Rewards', 'Task Detail'];
const initialSurveyQuestion = {
  title: '',
  options: [''],
};

function CampaignNewForm({ isEdit, currentCampaign }) {
  const navigate = useNavigate();
  const { enqueueSnackbar } = useSnackbar();
  const [activeStep, setActiveStep] = useState(0);
  const [campaignType, setCampaignType] = useState('Task');

  // const VALIDATION_SCHEMAS = [CampaignTypeSchema, TaskAndRewardsSchema, TaskDetailSchema];

  const VALIDATION_SCHEMAS = {
    0: {
      Task: CampaignTypeSchema,
      Contest: CampaignTypeSchema,
      Survey: CampaignTypeSchema,
      'Watch Ad': CampaignTypeSchema,
      Campaign: CampaignTypeSchema,
    },
    1: {
      Task: TaskAndRewardsSchema,
      Contest: ContestAndRewardsSchema,
      Survey: SurveyAndRewardsSchema,
      'Watch Ad': WatchAdSchema,
      Campaign: Yup.object(),
    },
    2: {
      Task: TaskDetailSchema,
      Contest: ContestDetailSchema,
      Survey: Yup.object(),
      'Watch Ad': WatchAdDetailSchema,
      Campaign: Yup.object(),
    },
  };

  const isLastStep = () => activeStep === STEPS.length - 1;

  const handlePrevStep = () => setActiveStep((prev) => prev - 1);

  const handleNextStep = () => setActiveStep((prev) => prev + 1);

  const formik = useFormik({
    enableReinitialize: true,
    initialValues: {
      brand: currentCampaign?.brand || '',
      campaignType: currentCampaign?.campaignType || 'Task',
      name: currentCampaign?.name || '',
      taskType: currentCampaign?.taskType || 'Video',
      totalBudget: currentCampaign?.totalBudget || undefined,
      perUser: currentCampaign?.perUser || '',
      starPerUser: currentCampaign?.starPerUser || undefined,
      continueAfterBudget: currentCampaign?.continueAfterBudget || '',
      tier: currentCampaign?.tier || 'Public',
      associatedBonus: currentCampaign?.associatedBonus || 'Bonus 1',
      isPublic: currentCampaign?.isPublic || false,
      headerImage: currentCampaign?.headerImage || undefined,
      doList: currentCampaign?.doList || [''],
      dontList: currentCampaign?.dontList || [''],
      igCaptionQuestions: currentCampaign?.igCaptionQuestions || [''],
      noOfPhotoRequired: currentCampaign?.noOfPhotoRequired || undefined,
      noOfVideoRequired: currentCampaign?.noOfVideoRequired || undefined,
      hashtags: currentCampaign?.hashtags || [],
      mentions: currentCampaign?.mentions || [],
      startDate: currentCampaign?.startDate ? new Date(currentCampaign.startDate) : null,
      endDate: currentCampaign?.endDate ? new Date(currentCampaign.endDate) : null,
      surveyQuestions: currentCampaign?.surveyQuestions || [initialSurveyQuestion],
      percentageColor: currentCampaign?.percentageColor || '',
      adVideo: currentCampaign?.adVideo || null,
      adVideoThumbnail: currentCampaign?.adVideoThumbnail || null,
    },
    validationSchema: VALIDATION_SCHEMAS[activeStep][campaignType],
    onSubmit: async (values, { setSubmitting, resetForm, setErrors }) => {
      if (isLastStep()) {
        try {
          alert(JSON.stringify(values));
          resetForm();
          setSubmitting(false);
          enqueueSnackbar(!isEdit ? 'Create success' : 'Update success', { variant: 'success' });
          navigate(PATH_DASHBOARD.campaign.list.all);
        } catch (error) {
          console.error(error);
          setSubmitting(false);
          setErrors(error);
        }
      } else {
        handleNextStep();
      }
    },
  });

  const { errors, values, handleSubmit, isSubmitting } = formik;

  const renderForm = () => {
    const forms = {
      0: {
        Task: <CampaignTypeForm formik={formik} onCampaignTypeChange={(val) => setCampaignType(val)} />,
        Contest: <CampaignTypeForm formik={formik} onCampaignTypeChange={(val) => setCampaignType(val)} />,
        Survey: <CampaignTypeForm formik={formik} onCampaignTypeChange={(val) => setCampaignType(val)} />,
        'Watch Ad': <CampaignTypeForm formik={formik} onCampaignTypeChange={(val) => setCampaignType(val)} />,
        Campaign: <CampaignTypeForm formik={formik} onCampaignTypeChange={(val) => setCampaignType(val)} />,
      },
      1: {
        Task: <TaskAndRewardsForm formik={formik} />,
        Contest: <ContestAndRewardsForm formik={formik} />,
        Survey: <SurveyAndRewardsForm formik={formik} />,
        'Watch Ad': <WatchAdForm formik={formik} />,
        Campaign: <div>Campaign Form</div>,
      },
      2: {
        Task: <TaskDetailForm formik={formik} />,
        Contest: <ContestDetailForm formik={formik} />,
        Survey: <div>Survey Detail Form</div>,
        'Watch Ad': <WatchAdDetailForm formik={formik} />,
        Campaign: <div>Campaign Form</div>,
      },
    };
    return forms[activeStep][campaignType];
  };

  console.log('Active Step: ', activeStep);
  console.log('*** ', values, errors);

  return (
    <FormikProvider value={formik}>
      <Form noValidate autoComplete="off" onSubmit={handleSubmit}>
        <Grid container justifyContent="center">
          <Grid item xs={12} md={8} sx={{ mb: 5 }}>
            <Steps steps={STEPS} activeStep={activeStep} />
          </Grid>
        </Grid>

        <Card sx={{ p: 3 }}>
          <Stack spacing={3}>
            {renderForm()}
            <Box sx={{ mt: 3, display: 'flex', justifyContent: 'flex-end' }}>
              {activeStep !== 0 && (
                <Button
                  sx={{ mr: 2 }}
                  variant="outlined"
                  onClick={isLastStep() ? () => console.log('Draft Clicked') : handlePrevStep}
                >
                  {isLastStep() ? 'Draft' : 'Back'}
                </Button>
              )}
              <LoadingButton type="submit" variant="contained" loading={isSubmitting}>
                {isLastStep() ? 'Setup Complete' : 'Next'}
              </LoadingButton>
            </Box>
          </Stack>
        </Card>
      </Form>
    </FormikProvider>
  );
}

CampaignNewForm.propTypes = {
  isEdit: PropTypes.bool,
  currentCampaign: PropTypes.object,
};

export default CampaignNewForm;
