import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import { isValid as isValidDate } from 'date-fns';
// material
import { styled } from '@mui/material/styles';
import { DateTimePicker } from '@mui/lab';
import { Card, FormHelperText, Grid, Stack, TextField, Typography } from '@mui/material';
// components
import { UploadAvatar, UploadSingleFile } from '../../upload';

import { fData } from '../../../utils/formatNumber';

const LabelStyle = styled(Typography)(({ theme }) => ({
  ...theme.typography.subtitle2,
  color: theme.palette.text.secondary,
  marginBottom: theme.spacing(1),
}));

const WatchAdDetailForm = ({ formik }) => {
  const { values, errors, touched, setFieldValue } = formik;

  const handleVideoDrop = useCallback(
    (acceptedFiles) => {
      const file = acceptedFiles[0];
      if (file) {
        setFieldValue('adVideo', {
          ...file,
          preview: URL.createObjectURL(file),
        });
      }
    },
    [setFieldValue]
  );

  const handleThumbnailDrop = useCallback(
    (acceptedFiles) => {
      const file = acceptedFiles[0];
      if (file) {
        setFieldValue('adVideoThumbnail', {
          ...file,
          preview: URL.createObjectURL(file),
        });
      }
    },
    [setFieldValue]
  );

  return (
    <Grid container spacing={3}>
      <Grid item xs={12} md={8}>
        <Stack spacing={3}>
          <Stack>
            <LabelStyle>Upload Ad Video</LabelStyle>
            <UploadSingleFile
              maxSize={3145728}
              accept="video/*"
              file={values.adVideo}
              onDrop={handleVideoDrop}
              error={Boolean(touched.adVideo && errors.adVideo)}
            />
            {touched.adVideo && errors.adVideo && (
              <FormHelperText error sx={{ px: 2 }}>
                {touched.adVideo && errors.adVideo}
              </FormHelperText>
            )}
          </Stack>
          <Stack>
            <LabelStyle>Video Thumbnail</LabelStyle>
            <UploadAvatar
              accept="image/*"
              file={values.adVideoThumbnail}
              maxSize={3145728}
              onDrop={handleThumbnailDrop}
              error={Boolean(touched.adVideoThumbnail && errors.adVideoThumbnail)}
              caption={
                <Typography
                  variant="caption"
                  sx={{
                    mt: 2,
                    mx: 'auto',
                    display: 'block',
                    textAlign: 'center',
                    color: 'text.secondary',
                  }}
                >
                  Allowed *.jpeg, *.jpg, *.png, *.gif
                  <br /> max size of {fData(3145728)}
                </Typography>
              }
            />
            <FormHelperText error sx={{ px: 2, textAlign: 'center' }}>
              {touched.adVideoThumbnail && errors.adVideoThumbnail}
            </FormHelperText>
          </Stack>

          <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
            <DateTimePicker
              label="Start date"
              value={values.startDate}
              inputFormat="dd/MM/yyyy hh:mm a"
              onChange={(date) => (isValidDate(date) ? setFieldValue('startDate', date) : null)}
              renderInput={(params) => (
                <TextField
                  {...params}
                  size="small"
                  fullWidth
                  error={Boolean(touched.startDate && errors.startDate)}
                  helperText={touched.startDate && errors.startDate}
                  sx={{ mb: 3 }}
                />
              )}
            />
            <DateTimePicker
              label="End date"
              value={values.endDate}
              inputFormat="dd/MM/yyyy hh:mm a"
              onChange={(date) => (isValidDate(date) ? setFieldValue('endDate', date) : null)}
              renderInput={(params) => (
                <TextField
                  {...params}
                  size="small"
                  fullWidth
                  error={Boolean(touched.endDate && errors.endDate)}
                  helperText={touched.endDate && errors.endDate}
                  sx={{ mb: 3 }}
                />
              )}
            />
          </Stack>
        </Stack>
      </Grid>
      <Grid item xs={12} md={4} sx={{ height: 'auto' }}>
        <Card sx={{ p: 3, height: '100%' }}>Later Phase</Card>
      </Grid>
    </Grid>
  );
};

WatchAdDetailForm.propTypes = {
  formik: PropTypes.object,
};

export default WatchAdDetailForm;
