import * as Yup from 'yup';
import { nameRegExp } from '../../utils/constants/regexs';

export const CampaignTypeSchema = Yup.object().shape({
  brand: Yup.string().matches(nameRegExp, 'Cannot include special characters.').required('Brand is required'),
  campaignType: Yup.string(),
});

export const TaskAndRewardsSchema = Yup.object().shape({
  name: Yup.string().matches(nameRegExp, 'Cannot include special characters.').required('Name is required'),
  taskType: Yup.string(),
  tier: Yup.string(),
  associatedBonus: Yup.string(),
  isPublic: Yup.boolean().required('Status is required'),
});

export const TaskDetailSchema = Yup.object().shape({
  headerImage: Yup.mixed(),
  description: Yup.string(),
  totalBudget: Yup.number(),
  perUser: Yup.string(),
  starPerUser: Yup.number(),
  continueAfterBudget: Yup.string(),
  doList: Yup.array().of(Yup.string()),
  dontList: Yup.array().of(Yup.string()),
  igCaptionQuestions: Yup.array().of(Yup.string()),
  noOfPhotoRequired: Yup.number(),
  noOfVideoRequired: Yup.number(),
  hashtags: Yup.array().of(Yup.string()),
  mentions: Yup.array().of(Yup.string()),
  startDate: Yup.date().nullable(true),
  endDate: Yup.date().when('startDate', (startDate, schema) =>
    startDate ? schema.min(startDate, 'End date must be later than start date') : schema.nullable(true)
  ),
});

export const ContestAndRewardsSchema = Yup.object().shape({
  name: Yup.string().matches(nameRegExp, 'Cannot include special characters.').required('Name is required'),
  contestType: Yup.string(),
  tier: Yup.string(),
  associatedBonus: Yup.string(),
  isPublic: Yup.boolean().required('Status is required'),
});

export const ContestDetailSchema = Yup.object().shape({
  headerImage: Yup.mixed(),
  description: Yup.string(),
  totalBudget: Yup.number(),
  winnerToken: Yup.string(),
  starPerUser: Yup.number(),
  continueAfterBudget: Yup.string(),
  doList: Yup.array().of(Yup.string()),
  dontList: Yup.array().of(Yup.string()),
  igCaptionQuestions: Yup.array().of(Yup.string()),
  noOfPhotoRequired: Yup.number(),
  noOfVideoRequired: Yup.number(),
  hashtags: Yup.array().of(Yup.string()),
  mentions: Yup.array().of(Yup.string()),
  contestStartDate: Yup.date().nullable(true),
  contestEndDate: Yup.date().when('contestStartDate', (contestStartDate, schema) =>
    contestStartDate ? schema.min(contestStartDate, 'End date must be later than start date') : schema.nullable(true)
  ),
  votingStartDate: Yup.date().nullable(true),
  votingEndDate: Yup.date().when('votingStartDate', (votingStartDate, schema) =>
    votingStartDate ? schema.min(votingStartDate, 'End date must be later than start date') : schema.nullable(true)
  ),
  winnerDate: Yup.date().nullable(true),
});

export const SurveyAndRewardsSchema = Yup.object().shape({
  name: Yup.string().matches(nameRegExp, 'Cannot include special characters.').required('Name is required'),
  description: Yup.string(),
  percentageColor: Yup.string(),
  starPerUser: Yup.number(),
  startDate: Yup.date().nullable(true),
  endDate: Yup.date().when('startDate', (startDate, schema) =>
    startDate ? schema.min(startDate, 'End date must be later than start date') : schema.nullable(true)
  ),
  surveyQuestions: Yup.array().of(Yup.object().shape({ title: Yup.string(), options: Yup.array().of(Yup.string()) })),
});

export const WatchAdSchema = Yup.object().shape({
  name: Yup.string().matches(nameRegExp, 'Cannot include special characters.').required('Name is required'),
  taskType: Yup.string(),
  totalBudget: Yup.number(),
  perUser: Yup.string(),
  starPerUser: Yup.number(),
  continueAfterBudget: Yup.string(),
  tier: Yup.string(),
  associatedBonus: Yup.string(),
  isPublic: Yup.boolean().required('Status is required'),
});

export const WatchAdDetailSchema = Yup.object().shape({
  adVideo: Yup.mixed().required('Ad Video is required'),
  adVideoThumbnail: Yup.mixed().required('Thumbnail is required'),
  startDate: Yup.date().nullable(true),
  endDate: Yup.date().when('startDate', (startDate, schema) =>
    startDate ? schema.min(startDate, 'End date must be later than start date') : schema.nullable(true)
  ),
});
