import { useParams, useLocation } from 'react-router-dom';
// material
import { Container } from '@mui/material';
// routes
import { PATH_DASHBOARD } from '../routes/paths';
// hooks
import useSettings from '../hooks/useSettings';
// components
import Page from '../components/Page';
import HeaderBreadcrumbs from '../components/HeaderBreadcrumbs';
import CampaignNewForm from '../components/campaign/CampaignNewForm';

// ----------------------------------------------------------------------

function CampaignCreate() {
  const { themeStretch } = useSettings();
  const { pathname } = useLocation();
  const { name } = useParams();
  const isEdit = pathname.includes('edit');

  const currentCampaign = {};

  return (
    <Page title="Campaign: Create a new campaign">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading={!isEdit ? 'Create Campaign' : 'Edit Campaign'}
          links={[
            { name: 'Campaign', href: PATH_DASHBOARD.campaign.list.all },
            { name: !isEdit ? 'Create Campaign' : name },
          ]}
        />
        <CampaignNewForm isEdit={isEdit} currentCampaign={currentCampaign} />
      </Container>
    </Page>
  );
}

export default CampaignCreate;
